/* === Основные стили === */
.container {
  width: 1400px;
  max-width: 1400px;
  text-align: center;
  margin: auto;
  padding: 10px;
}

.title {
  font-family: 'Poppins', sans-serif;
  color: #4e4e4e;
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 30px;
}

.skillsGrid {
  display: flex;
  justify-content: center;
  flex-direction: row;
  gap: 20px;
  margin: auto;
}

/* === Карточки === */
.card {
  background: #bebebe;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cardContent {
  text-align: start;
}

.cardContent h3,
.lastCardDesc h3 {
  font-family: 'Poppins';
  color: #4e4e4e;
  font-size: 30px;
  margin: 0;
}

.cardContent p,
.lastCardDesc p {
  font-family: 'Poppins', sans-serif;
  font-size: 19px;
  color: #555;
  margin: 0 0 10px 0;
}

.skillsGrid > div:nth-child(1) {
  height: 840px;
  width: 415px;
}

.skillsGrid > div:nth-child(3) {
  padding: 0;
}

.skillsGrid > div:first-child .cardContent {
  width: 310px;
  height: 478px;
}

.skillsGrid .card:nth-child(1) .cardImage {
  width: 290px;
  height: 290px;
}

.secondBlock {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.secondBlock .card {
  width: 440px;
  height: 560px;
}

.secondBlock .card:nth-child(1) .cardImage {
  width: 236px;
  height: 236px;
}

.secondBlock .card:nth-child(2) .cardImage {
  width: 353px;
  height: 236px;
}

.secondBlock .card .cardContent p {
  width: 310px;
}

.cardLast {
  display: flex;
  background: #bebebe;
  border-radius: 10px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
  align-items: center;
  height: 257px;
}

.cardLast .cardImage {
  width: 300px;
  height: 200px;
  margin-left: 70px;
}

.cardLast p {
  width: 396px;
}

.lastCardDesc {
  padding: 20px 0 20px 60px;
}

.lastCardDesc button {
  margin-right: 100%;
}

/* === Группа из трёх карточек === */
.threeCard {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* === Кнопка === */
.button {
  width: 200px;
  height: 50px;
  background: #4A6FA5;
  color: white;
  border: none;
  padding: 10px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s;
  margin-top: 15px;
}

.button:hover {
  background: #0056b3;
}

/* === Адаптивность === */

@media (min-width: 1440px) {
  .container {
    max-width: 1400px;
  }
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .container {
    width: 90%;
  }
  .secondBlock {
    flex-direction: row;
  }
  .cardLast {
    max-width: 800px;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .container {
    width: 95%;
  }


  .skillsGrid,
  .secondBlock {
    flex-direction: column;
  }

  .skillsGrid > div:nth-child(1) {
    margin: auto;
    margin-top: 30px;
    height: 720px;
  }

  .secondBlock > div:nth-child(1) {
    margin: auto;
    margin-top: 30px;
    height: 720px;
  }

  .secondBlock > div:nth-child(1) .cardContent {
    width: 310px;
    height: 410px;
  }

  .secondBlock > div:nth-child(1) .cardContent h3{
    margin-top: 30px;
    margin-bottom: 40px;
  }

  .secondBlock > div:nth-child(1) .cardContent p{
    margin-bottom: 40px;
  }


  .secondBlock > div:nth-child(2) .cardContent {
    width: 310px;
    height: 410px;
  }

  .secondBlock > div:nth-child(2) .cardContent h3{
    margin-top: 30px;
    margin-bottom: 40px;
  }

  .secondBlock > div:nth-child(2) .cardContent p{
    margin-bottom: 40px;
  }



  .secondBlock > div:nth-child(2) {
    margin: auto;
    margin-top: 30px;
    height: 720px;
  }

 .cardLast {
  width: 440px;
  height: 720px;
  margin: auto;
  flex-direction: column-reverse;
  justify-content: space-between;
 }

  
 .lastCardDesc {
  width: 310px;
  padding: 0px;
  height: 450px;
 }


 .lastCardDesc h3{
  text-align: start;
  margin-top: 30px;
  margin-bottom: 40px;
 }

 .lastCardDesc p{
  width: 310px;
  text-align: start;
  margin-bottom: 40px;
 }

  .skillsGrid .card:nth-child(1) .cardImage {
    width: 300px;
    height: 300px;
  }
  .secondBlock .card:nth-child(1) .cardImage {
    width: 300px;
    height: 300px;
  }
  .secondBlock .card:nth-child(2) .cardImage {
    width: 370px;
    height: 260px;
  }
  .cardLast .cardImage {
    width: 400px;
    height: 250px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 0px;
  }
}

@media (max-width: 767px) {
  .container {
    width: 95%;
  }
  .skillsGrid,
  .secondBlock {
    flex-direction: column;
  }

  .title {
    font-size: 30px;
    font-weight: 600;
    margin-top: 60px;
    margin-bottom: 10px;
  }


  .skillsGrid > div:nth-child(1) {
    width: 90%;
    margin: auto;
    margin-top: 30px;
    height: 630px;
  }

  .skillsGrid > div:nth-child(1) .cardContent {
    width: 90%;
    height: 100%;
  }

  .skillsGrid > div:nth-child(1) .cardContent  h3{
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 22px;
    font-weight: 900;
  }

  .skillsGrid > div:nth-child(1) .cardContent  p{
    font-size: 17px;
    font-weight: 400;
  }

  .secondBlock > div:nth-child(1) {
    width: 90%;
    margin: auto;
    margin-top: 30px;
    height: 580px;
  }

  .secondBlock > div:nth-child(1) .cardContent {
    width: 90%;
  }


  .secondBlock > div:nth-child(1) .cardContent h3{
    font-weight: 900;
    font-size: 22px;
    margin-top: 30px;
    margin-bottom: 40px;
  }

  .secondBlock > div:nth-child(1) .cardContent p{
    font-weight: 400;
    font-size: 17px;
    width: 90%;
    margin-bottom: 40px;
  }


  .secondBlock > div:nth-child(2) .cardContent {
    width: 90%;
  }

  .secondBlock > div:nth-child(2) .cardContent h3{
    font-weight: 900;
    font-size: 22px;
    margin-top: 50px;
    margin-bottom: 40px;
  }

  .secondBlock > div:nth-child(2) .cardContent p{
    font-weight: 400;
    font-size: 17px;
    margin-bottom: 40px;
    width: 90%;
  }



  .secondBlock > div:nth-child(2) {
    width: 90%;
    margin: auto;
    margin-top: 30px;
    height: 560px;
  }

 .cardLast {
  width: 90%;
  height: 510px;
  margin: auto;
  flex-direction: column-reverse;
  justify-content: space-between;
 }

  
 .lastCardDesc {
  display: flex;
  flex-direction: column;
  width: 90%;
  padding: 0px;
  height: 100%;
 }


 .lastCardDesc h3{
  font-weight: 900;
  font-size: 22px;
  text-align: start;
  margin-top: 30px;
  margin-bottom: 40px;
 }

 .lastCardDesc p{
  font-weight: 400;
  font-size: 17px;
  width: 310px;
  text-align: start;
  margin-bottom: 40px;
 }

  .skillsGrid .card:nth-child(1) .cardImage {
    width: 212px;
    height: 212px;
  }
  .secondBlock .card:nth-child(1) .cardImage {
    width: 204px;
    height: 204px;
  }
  .secondBlock .card:nth-child(2) .cardImage {
    width: 236px;
    height: 157px;
  }
  .cardLast .cardImage {
    width: 232px;
    height: 154px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 0px;
  }

  .cardLast .button {
    width: 200px;
    margin: 0px;
  }
}
