/* Base Styles */
.heroSection {
    width: 1400px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #dfdfdf; /* Темный фон */
}

.heroContent {
    max-width: 50%;
    padding: 30px;
    margin-top: 30px;
}



.afterHeroTittle {
    display: flex;
    justify-content: space-between;
}

.heroAfterEl {
    width: 85px;
    height: 85px;
    margin-right: 100px;
    margin-top: 100px;
}

.heroTitle {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    text-align: left;
    font-weight: 600;
    font-size: 64px;
    margin: 0;
    line-height: 1.25;
    width: 500px;
    color: #4e4e4e; /* Лунный серебристый */
}

.heroDescription {
    display: block;
    font-family: "Poppins";
    line-height: 1.5;
    font-weight: 400;
    font-style: normal;
    text-align: start;
    font-size: 24px;
    padding-top: 30px;
    padding-bottom: 30px;
    width: 438px;
    color: #919191; /* Светло-серый текст */
}


.getStartedBtn {
    width: 420px;
    font-family: 'Poppins';
    height: 105px;
    font-size: 24px;
    font-weight: 600;
    border-radius: 10px;
    line-height: 1.5;
    background-color: #4A6FA5;
    color: #fdfdfd;
    border: 1px solid #fdfdfd;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.getStartedBtn:hover {
    background-color: #1E90FF;
}

.heroImage img {
    width: 600px;
    height: 600px;
    border-radius: 10px;
    margin-bottom: 40px;
}

/* Media Queries */

/* Large Desktop (1440px и выше) */
@media (min-width: 1440px) {
    /* Здесь можно увеличить размеры шрифтов и изображений */
}

/* Desktop (1024px - 1439px) */
@media (min-width: 1024px) and (max-width: 1439px) {
    .heroSection {
        width: 90%; /* Уменьшаем ширину контейнера */
    }
    .heroTitle {
        font-size: 50px;
    }
    .heroImage img {
        width: 460px;
        height: 460px;
    }

    .getStartedBtn {
        width: 350px;
    }

    .heroDescription {
        width: 320px;
    }

    .btn {
        width: 350px;
    }

    .getStartedBtn {
        font-size: 18px;
        line-height: 1.5;
       width: 305px;
        margin: auto;
    }

    .btn {
        width: 305px;
    }

}

/* Tablet (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
    .heroSection {
        width: 90%;
        flex-direction: column-reverse; /* Перестраиваем в колонку */
        align-items: center;
    }
    .heroContent {
        max-width: 90%;
        text-align: center;
        margin: auto;
    }
    .heroTitle {
        font-size: 48px;
        width: 500px;
        margin: auto;
        text-align: center;
    }
    .heroDescription {
        font-size: 20px;
        width: auto;
        width: 390px;
        text-align: center;
        margin: auto;
    }
    .getStartedBtn {
        width: 300px;
        height: 90px;
        font-size: 20px;
    }
    .heroImage img {
        width: 424px;
        height: 424px;
    }

    .heroAfterEl {
        display: none;
    }
}

/* Mobile (до 767px) */
@media (max-width: 767px) {
    .heroSection {
        flex-direction: column-reverse;
        align-items: center;
        width: 90%;
    }
    .heroContent {
        max-width: 90%;
        margin-top: 0px;
        padding: 0px;
    }
    .heroTitle {
        font-size: 24px;
        width: 60%;
        font-weight: 700;
        margin: auto;
        text-align: center;
    }
    .heroDescription {
        font-size: 17px;
        font-weight: 500;
        padding: 20px 0;
        margin: auto;
        width: 70%;
        text-align: center;
    }
    .getStartedBtn {
        width: 100%;
        height: 60px;
        font-size: 16px;
        font-weight: 500;
        margin: auto;
    }

    .btn {
        width: 90%;
        margin: auto;
    }
    .heroImage img {
        width: 424px;
        height: 424px;
        margin-top: 60px;
        margin-bottom: 10px;
    }

    .heroAfterEl {
        display: none;
    }
}

@media(max-width: 450px) {
    .heroImage img {
        width: 300px;
        height: 300px;
    }
}