/* Основной контейнер подвала */
.footerContainer {
    width: 1500px;
    margin: auto;
    margin-top: 50px;
    border-top: 1px solid #A3AEB9;
    padding: 20px 0;
}

/* Хедер внутри футера */
.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    height: 60px;
}

/* Логотип */
.logo h2 {
    font-weight: 600;
    font-size: 35px;
    color: #8f8f8f;
    font-family: 'Poppins', sans-serif;
}

.logo span {
    color: #007bff; /* Добавь свой цвет */
}

/* Навигация */
.nav {
    flex-grow: 1;
    text-align: center;
}

.navList {
    color: #4e4e4e;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: end;
}

.navItem {
    font-family: 'Poppins', sans-serif;
    margin: 0 15px;
    font-size: 20px;
    line-height: 1.5;
    cursor: pointer;
    transition: color 0.3s ease;
}

.navItem:hover {
    color: #A3AEB9; /* Цвет при наведении */
}

/* Кнопка призыва к действию */
.ctaButton {
    width: 220px;
    height: 70px;
    font-size: 24px;
    font-weight: 400;
    border-radius: 5px;
    background-color: #1D2D44;
    color: #A3AEB9;
    border: 2px solid #A3AEB9;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.ctaButton:hover {
    background-color: #0056b3; /* Цвет при наведении */
}

/* ====== Медиа-запросы для адаптива ====== */

/* Для больших экранов (1400px и больше) */
@media (min-width: 1400px) {
    .footerContainer {
        width: 1500px;
    }
}

/* Для стандартных десктопов (1024px - 1399px) */
@media (max-width: 1399px) {
    .footerContainer {
        width: 90%;
    }

    .logo h2 {
        font-size: 30px;
    }

    .navItem {
        font-size: 18px;
        margin: 0 10px;
    }

    .ctaButton {
        width: 200px;
        height: 60px;
        font-size: 20px;
    }
}

/* Для планшетов (768px - 1023px) */
@media (max-width: 1023px) {
    .footerContainer {
        width: 90%;
        padding: 15px 0;
    }

    .header {
        flex-direction: column; /* Переносим элементы вертикально */
        align-items: center;
        padding: 20px;
        gap: 20px; /* Расстояние между элементами */
    }

    .logo h2 {
        font-size: 28px;
    }

    .navList {
        justify-content: center;
        gap: 15px;
    }

    .navItem {
        font-size: 16px;
        margin: 0 8px;
    }

    .ctaButton {
        width: 180px;
        height: 55px;
        font-size: 18px;
    }
}

/* Для мобильных устройств (до 767px) */
@media (max-width: 767px) {
    .footerContainer {

        width: 90%;
        margin: auto;
        margin-top: 30px;
    }

    .footer {
        padding: 0;
        display: flex;
        justify-content: space-between;
    }

    .logo h2 {
        font-size: 24px;
        text-align: center;
    }

    .navList {
        margin-left: 40px;
        justify-content: flex-end;
        align-items: center;
        gap: 10px;
    }

    .navItem img {
        width: 26px;
        height: 26px;
    }

}
