/* Основная секция цен */
.priceSection {
    width: 1400px;
    margin: auto;
    margin-top: 100px;
}

/* Список ценовых блоков */
.priceSection ul {
    margin-top: 100px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    gap: 20px; /* Добавлен небольшой отступ между элементами */
    padding: 0;
}

/* Каждый ценовой блок */
.priceSection li {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

/* Изображения */
.priceImg1 {
    width: 320px;
    height: 320px;
    object-fit: cover;
    border-radius: 10px;
}

.priceImg2 {
    width: 320px;
    height: 320px;
    object-fit: cover;
    border-radius: 10px;
}

.priceImg3 {
    width: 320px;
    height: 320px;
    object-fit: cover;
    border-radius: 10px;
}

/* Заголовки */
.priceSection h2 {
    font-family: 'Poppins';
    color: #4e4e4e;
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
}

.priceSection h4 {
    margin: 20px 0;
    text-align: start;
    width: 375px;
    font-size: 30px;
    line-height: 1.6;
    color: #555;
}

/* Описание */
.priceSection p {
    font-family: "Poppins";
    text-align: start;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.5;
    margin: 0 0 20px 0;
}

.priceDesc {
    width: 370px;
    color: #505050;
}

/* Блок с ценой */
.priceCost {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 240px;
    padding: 10px 0;
    color: white;
    background-color: #4A6FA5;
    border-radius: 5px;
    font-family: "Poppins";
}

.value {
    display: flex;
    align-items: center;
    font-size: 30px;
    line-height: 1.5;
    font-family: "Poppins";
}

/* ====== Медиа-запросы для адаптива ====== */

/* Для больших экранов (1400px и больше) */
@media (min-width: 1400px) {
    .priceSection {
        width: 1400px;
    }
}

/* Для стандартных десктопов (1024px - 1399px) */
@media (max-width: 1399px) {
    .priceSection {
        width: 90%;
    }

    .priceSection ul {
        gap: 15px;
        justify-content: space-around;
    }

    .priceImg {
        width: 280px;
        height: 280px;
    }

    .priceSection h4 {
        width: 300px;
        font-size: 26px;
    }

    .priceDesc {
        width: 320px;
    }

    .priceCost {
        width: 200px;
    }
}

/* Для планшетов (768px - 1023px) */
@media (max-width: 1023px) {
    .priceSection {
        width: 90%;
        margin-top: 80px;
    }

    .priceSection ul {
        flex-wrap: wrap; /* Перенос элементов на новую строку */
        justify-content: center;
        gap: 30px;
    }

    .priceSection li {
        width: 45%; /* Два элемента в строке */
        align-items: center;
    }

    .priceImg {
        width: 250px;
        height: 250px;
    }

    .priceSection h4 {
        width: 100%;
        text-align: center;
        font-size: 24px;
    }

    .priceDesc {
        width: 100%;
        text-align: center;
    }

    .priceCost {
        width: 180px;
    }
}

/* Для мобильных устройств (до 767px) */
@media (max-width: 767px) {
    .priceSection {
        width: 90%;
        margin: 50px auto;
    }

    .priceSection ul {
        flex-direction: column;
        gap: 60px;
        align-items: center;
        margin-top: 40px;
    }

    .priceSection li {
        width: 100%;
        max-width: 350px;
        align-items: center;
    }

    .priceImg1 {
        width: 248px;
        height: 248px;
    }

    
    .priceImg2 {
        width: 241px;
        height: 241px;
    }

    
    .priceImg3 {
        width: 257px;
        height: 197px;
    }

    .priceSection h2 {
        color: #4e4e4e;
        font-size: 30px;
        font-weight: 700;
        text-align: center;
        margin: 0px;
        }

    .priceSection h4 {
        font-size: 21px;
        width: 100%;
        text-align: center;
    }

    .priceDesc {
        font-size: 15px;
        width: 100%;
        text-align: center;
        padding: 0 10px;
    }

    .priceSection p {
        font-size: 17px;
        text-align: center;
    }

    .priceCost {
        width: 170px;
        padding: 8px 0;
    }

    .value {
        font-size: 21px;
    }

    .manth {
        font-size: 14px;
    }
}
