/* Base Styles */
.headerContainer {
    width: 1400px;
    margin: auto;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    height: 60px;
}

.logo h2 {
    font-weight: 600;
    font-size: 35px;
    color: #8f8f8f;
    font-family: 'Poppins', sans-serif;
}

.logo span {
    color: #007bff; /* Основной акцентный цвет */
}

.nav {
    flex-grow: 1;
    text-align: center;
}

.navList {
    color: #4e4e4e;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

.navItem {
    font-family: 'Poppins', sans-serif;
    margin: 0 15px;
    font-size: 20px;
    line-height: 1.5;
    cursor: pointer;
    transition: color 0.3s ease;
}

.navItem:hover {
    color: #A3AEB9; /* Цвет при наведении */
}

.ctaButton {
    width: 220px;
    height: 70px;
    font-size: 22px;
    font-weight: 500;
    border-radius: 10px;
    background-color: #dfdfdf;
    color: #818181;
    border: 2px solid #9b9b9b;
    font-family: "Poppins";
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.ctaButton:hover {
    background-color: #0056b3; /* Цвет при наведении */
}

.brg {
    display: none;
}

/* Media Queries */

/* Large Desktop (1440px и выше) */
@media (min-width: 1440px) {
    /* Здесь можно увеличить отступы, размеры шрифтов и элементов */
    .brg {
        display: none;
    }
}   

/* Desktop (1024px - 1439px) */
@media (min-width: 1024px) and (max-width: 1439px) {
    .headerContainer {
        width: 90%; /* Немного уменьшаем ширину */
    }
    .brg {
        display: none;
    }
}

/* Tablet (768px - 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
    .headerContainer{ 
        width: 90%;
    }

    .navList {
        display: none;
    }

    .brg {
        display: block;
    }
    
    .ctaButton {
       display: none;
    }
}

/* Mobile (до 767px) */
@media (max-width: 767px) {
    .headerContainer {
        width: 100%;
        position: fixed;
        background-color: #dfdfdf;
        border-bottom: 1px solid grey;
    }

    .logo h2 {
        font-size: 24px;
        margin: 0px;
    }
    
    .logo p {
        font-size: 24px;
    }

    .header {
        height: auto; /* Высота по содержимому */
        padding-top: 10px; 
        padding-left: 20px;
        padding-right: 20px;   
        padding-bottom: 10px;
    }

    .brg {
        display: block;
        width: 30px;
    }

    .navList {
        display: none;
    }


    .ctaButton {
        display: none;
    }
}
